import React, { useEffect } from 'react'

const Toolbar = ({ fontSize, setFontSize }) => {

    const toogleContrast = (init) => {
        if (window.sessionStorage) {
            const contrast = "contrast"
            const current = window.sessionStorage.getItem(contrast)
            window.sessionStorage.setItem(contrast, !init || (init && current === undefined) ? !JSON.parse(current || true) : (current || false))
        }
    }

    useEffect(() => {
        toogleContrast(true)
    }, [])

    return (
        <React.Fragment>
            <div className="app-toolbar" style={{ fontSize: `${fontSize}%` }}>
                <div className="app-toolbar--left">
                    <div className="app-toolbar--item" onClick={() => setFontSize(fontSize + 10)}>
                        <span>A+</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => setFontSize(fontSize - 10)}>
                        <span>A-</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => toogleContrast(false)}>
                        <span>Wersja kontrastowa</span>
                    </div>
                </div>
                <div className="app-toolbar--right">
                    <div className="app-toolbar--item">
                        <span>EN</span>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Toolbar